import React, { useState } from 'react';
import { useLocation } from 'react-router-dom';

import Sparkle from "../../assets/images/sparkle.svg";
import Notification from "../../assets/images/notification.svg";
import EditData from './EditData';

const EditPrompts = () => {
    const location = useLocation();
    const { state } = location;
    console.log(location, state, "Edit");

    const [editableData, setEditableData] = useState(state);



    const handleShowReflection = (item) => {
        console.log(item);
    };

    return (
        <div>
            <div id="calendar" className='evening-img'>
                <div className='container'>
                    <div className='row mt-4'>
                        <div className='col-md-6'>
                            <div className='d-flex'>
                                <div className='sparkle-img'>
                                    <img src={Sparkle} alt='sparkle' />
                                </div>
                                <div className='heading-content'>
                                    <h1>Good evening, Ophelia!</h1>
                                </div>
                            </div>
                            <div className='date'>
                                <p>May 20, 2024</p>
                            </div>
                        </div>
                        <div className='col-md-6'>
                            <div className='text-end'>
                                <img src={Notification} alt='notification' />
                            </div>
                        </div>
                    </div>
                    <EditData
                        handleShowReflection={handleShowReflection}
                        editableData={editableData}

                    />
                </div>
            </div>
        </div>
    );
};

export default EditPrompts;
