import React, { useState } from 'react';
import Down from "../../assets/images/downarrow.svg";
import Daily from "../../assets/images/daily-prompt.svg";

const EditData = ({ handleShowReflection, editableData, }) => {
    const [expandedSection, setExpandedSection] = useState(null);
    const [isModalOpen, setIsModalOpen] = useState(false);

    const toggleExpand = (section) => {
        setExpandedSection(prevSection => prevSection === section ? null : section);
    };

    const openModal = () => {
        setIsModalOpen(true);
    };

    const closeModal = () => {
        setIsModalOpen(false);
    };

    const handleModalOpen = () => {
        setIsModalOpen(true);
    };

    return (
        <div>
            <div className=''>
                <div className='row mt-4 justify-content-between'>
                    <div className='col-md-12'>
                        <div className=' section-prompt overflow-hidden'>
                            <div className='position-relative background-images'>
                                <div className='d-flex pt-3'>
                                    <div className='sparkle-img'>
                                        <img src={Daily} alt='Daily' />
                                    </div>
                                    <div className='text-start px-3'>
                                        <p className='prompt-content mb-1'>Edit PROMPTS</p>
                                        <p className='prompts-text mb-1'>
                                            Prompts to follow every day
                                        </p>
                                    </div>
                                </div>
                                <div className='prompt-card mb-4 pt-4'>
                                    <div className='today-section'>
                                        {editableData.items.map((item, index) => (
                                            <div key={index}>
                                                <div className='input-texts pt-2 d-flex mb-3'>
                                                    <div className='input-content' onClick={() => handleShowReflection(item)}>
                                                        <img src={Down} alt='downarrow' width={20} height={20} />
                                                        <p className='reflection pt-2 px-3'>{item}</p>
                                                    </div>
                                                    <div className='pe-3'>
                                                        <button
                                                            className='plus-button'
                                                            style={{
                                                                backgroundColor: expandedSection === `section${index}` ? '#fff' : '#678561',
                                                                color: expandedSection === `section${index}` ? '#678561' : '#fff'
                                                            }}
                                                            onClick={() => toggleExpand(`section${index}`)}
                                                        >
                                                            {expandedSection === `section${index}` ? '-' : '+'}
                                                        </button>
                                                    </div>
                                                </div>
                                                {expandedSection === `section${index}` && (
                                                    <div className='expanded-content text-start px-3'>
                                                        <p>Here is the expanded content for {item}...</p>
                                                    </div>
                                                )}
                                            </div>
                                        ))}
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default EditData;
